.pop-up-attachments {
    background-color: #fff;
    box-shadow: 0 8px 16px -4px rgba(9, 30, 66, 0.25), 0 0 0 1px rgba(9, 30, 66, 0.08);
    color: gray;
    padding: 10px;
    border-radius: 3px;
    width: 230px;
    position: absolute;
    top: 218px;
    right: -115px;
    transform: translate(-50%, 0);
    z-index: 99;
    @include for-mobile-layout {
        top: 95%;
        right: 5%;
    }
    .attach-choice {
        color: #172b4d;
        font-size: 14px;
    }
    .attach-label {
        font-weight: 700;
        color: #172b4d;
        font-size: 12px;
        line-height: 16px;
        margin-top: 12px;
        margin-bottom: 4px;
    }

    .attach-input {
        margin: 4px 0 12px;
        width: 100%;
        outline: none;
        border: none;
        box-shadow: inset 0 0 0 2px #dfe1e6;
        background-color: #fafbfc;
        border-radius: 3px;
        line-height: 20px;
        margin-bottom: 12px;
        padding: 8px 12px;
        color: #172b4d;
    }

    .pop-up-attach-btn {
        background-color: rgba(9, 30, 66, 0.04);
        box-shadow: none;
        border: none;
        cursor: pointer;
        font-weight: 400;
        line-height: 20px;
        padding: 6px 12px;
        text-align: center;
        margin: 0;
        outline: none;
        border-radius: 3px;
    }
}
input[type="file"] {
    opacity: 0; /* make transparent */
    z-index: -1; /* move under anything else */
    position: absolute; /* don't let it take up space */
}
