.card-preview {
    cursor: pointer;
    background: white;
    margin: 8px 1px 8px 8px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.12);
    box-shadow: 0 1px 0 rgba(9, 45, 66, 0.25);
    font-size: 15px;
    overflow-wrap: break-word;
    min-height: 18px;
    font-family: "Roboto";
    &:first-child{
        margin-top: 0;
    }
    &:last-child{
        margin-bottom: 0;
    }
    &
    .card-preview-summary-icons{
       display: flex;
    }

    .card-preview-icons {
        width: 100%;
        padding: 7px;
        .card-preview-bottom{
            margin-top: 2px;
        }
    }
    .icon-preview{
        font-size: 1rem;
        color: gray;
        display: flex;
        align-items: center;
        padding-inline-end: 5px;
    }
    .card-preview-edit-icon {
        opacity: 0;
        color: gray;
        font-size: 1.3rem;
    }
    .icons{
        font-size: 1.1rem;
        
    }
    &:hover {
        white-space: normal;
        .card-preview-edit-icon {
            opacity: 1;
        }
    }
    .icons-container{
        height: 1.5rem;
    }
    .preview-members{
        justify-content: flex-end;
    }
}



.card-preview-edit-card {
    & > form > input {
        border-radius: 3px;
        border: none;
        resize: none;
        outline: none;
        font-size: 15px;
        padding: 5px;
        scroll-behavior: auto;
        width: 100%;
        margin: 5px;
    }
    & > form > button {
        background-color: #5aac44;
        box-shadow: none;
        border: none;
        color: #fff;
        font-size: 14px;
        line-height: 20px;
        font-weight: 400;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 3px;
        cursor: pointer;
        padding: 6px 12px;
        text-decoration: none;
        font-family: "Roboto";
        margin: 5px;
    }
}

.card-preview-color {
    height: 15px;
    margin-top: 0;
    border-radius: 3px 3px 0 0;
}
