.stack-preview-card {
    margin: 0 4px;
    display: inline-block;
    max-height: 83vh;
    // min-height: 15vh;
    @include for-mobile-layout {
        max-height: 78vh;
    }

    // .stack-menu-header {
    //     display: flex;
    //     justify-content: flex-end;
    //     align-items: baseline;
    //     & > .title-stack {
    //         padding-inline-end: 80px;
    //     }
    // }

    .stack-title {
        cursor: pointer;
        padding: 10px;
        overflow-wrap: break-word;
        &:hover {
            .stack-preview-edit-icon,
            .stack-preview-delete-icon {
                opacity: 1;
            }
        }
        .stack-3dots-menu {
            color: gray;
        }
        .bg-modal-stack {
            position: fixed;
            width: 100vw;
            height: 100vh;
        }
        .stack-menu-modal {
            position: absolute;
            top: 40px;
            left: -31px;
            background-color: white;
            z-index: 5;
            border-radius: 3px;
            box-shadow: 0 8px 16px -4px rgba(9, 30, 66, 0.25), 0 0 0 1px rgba(9, 30, 66, 0.08);
            width: 304px;
            .stack-menu-header {
                height: 40px;
                position: relative;
                margin-bottom: 8px;
                text-align: center;
                .title-stack {
                    box-sizing: border-box;
                    color: #5e6c84;
                    display: block;
                    line-height: 45px;
                    border-bottom: 1px solid rgba(9, 30, 66, 0.13);
                    margin: 0 12px;
                    overflow: hidden;
                    padding: 0 32px;
                    position: relative;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    z-index: 1;
                }
                .close-menu-btn {
                    color: #6b778c;
                    padding: 10px 12px 10px 8px;
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 2;
                }
            }
            & > ul {
                list-style: none;
                padding: 0 12px 12px;
            }
            & > ul > li {
                padding: 6px 12px;
            }
        }
    }
    .stack-title-words {
        font-family: "Roboto-Bold";
        font-weight: 700;
        font-size: 1.2rem;
        margin-block-start: 0;
        margin-block-end: 0;
    }
    .stack-preview-edit-icon,
    .stack-preview-delete-icon {
        opacity: 0;
        color: gray;
        font-size: 1.2rem;
    }

    .add-new-card {
        font-family: "Roboto";
        margin: 8px;
        padding: 4px 8px;
        position: relative;
        border-radius: 3px;
        color: #5e6c84;
        min-height: 38px;
        max-height: 38px;
        &:hover {
            background-color: rgba(9, 30, 66, 0.08);
            color: #172b4d;
            cursor: pointer;
        }
    }
    .add-icon {
        color: #6b778c;
        margin-right: 2px;
    }
}

.edit-stack {
    & > form > input {
        border-radius: 3px;
        border: none;
        resize: none;
        outline: none;
        font-size: 15px;
        padding: 5px;
        scroll-behavior: auto;
    }
    & > form > button {
        background-color: #5aac44;
        box-shadow: none;
        border: none;
        color: #fff;
        font-size: 14px;
        line-height: 20px;
        font-weight: 400;
        box-sizing: border-box;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 3px;
        cursor: pointer;
        padding: 6px 12px;
        text-decoration: none;
        font-family: "Roboto";
    }
}
